import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { withStyles } from '@material-ui/core/styles';
import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import ListItemText from '@material-ui/core/ListItemText';
import Avatar from '@material-ui/core/Avatar';
import ImageIcon from '@material-ui/icons/Image';
import WorkIcon from '@material-ui/icons/Work';
import BeachAccessIcon from '@material-ui/icons/BeachAccess';

class NoteList extends Component {
    constructor(props){
        super(props);
    }
    shouldComponentUpdate(nextProps,nextState){
        return this.props.noteTotalSize == nextProps.notes.length && nextProps.notes != this.props.notes;
    }
    render(){
        var notes=this.props.notes;
        var items = notes.map((note)=>{
            return (<ListItem onClick={this.handleClick.bind(this,note.content)}>
                <ListItemText primary={note.name} secondary={note.date} />
            </ListItem>)
        });
        return (
            <div className='NodeList'>
                <List >
                    {items}
                </List>
            </div>
        );
    }



    handleClick = (content) => {
        //console.log(e)
        this.props.handleOnItemClick(content);
    } 
}

export default NoteList;
